:root {
  --color-primary-light: #780206;
  --color-primary-dark: #061161;

  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;

  --color-grey-dark-1: #333;
  --color-grey-dark-2: #777;
  --color-grey-dark-3: #999;

  --shadow-dark: 0 2rem 6rem rgba(0,0,0,0.3);
  --shadow-light: 0 2rem 5rem rgba(0,0,0,.06);
}

.App {
  text-align: center;
  font-weight: 100;
  line-height: 1.6;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 5s linear;
          animation: App-logo-spin infinite 5s linear;
  margin-top: 25px;
  height: 80px;
  width: 80px;
  border-radius: 50%;
}

.App-header {
  color: var(--color-grey-dark-2);
  background-image: -webkit-gradient(linear, left top, right bottom, from(var(--color-primary-light)), to(var(--color-primary-dark)));
  background-image: -webkit-linear-gradient(left top, var(--color-primary-light), var(--color-primary-dark));
  background-image: -o-linear-gradient(left top, var(--color-primary-light), var(--color-primary-dark));
  background-image: linear-gradient(to right bottom, var(--color-primary-light), var(--color-primary-dark));
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-box-shadow: var(--shadow-light);
          box-shadow: var(--shadow-light);
  padding: 5px 20px;
  color: white;
}

.App-title {
  font-size: 3em;
  font-weight: 100;
  text-shadow: var(--shadow-dark);
}

.App-intro {
  font-size: large;
}

div#chart {
  margin: 0 40px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.c3-tooltip {
  border: 1px solid var(--color-grey-light-4);
  background: #fff;
  text-align: left;
  padding: 10px;
}



